.Cotainer {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #333;
  color: #fff;
  padding-top: 30px;
}
.title {
  font-size: 30px;
  color: #d78c2f;
  letter-spacing: 1px;
}
.img-container {
  width: 90%;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 5px 5px 10px rgb(0 0 0 / 50%);
}
.img-container img {
  width: 100%;
}
.overwiev {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 15px;
  padding-top: 20px;
}

.abaut {
  width: 90%;
  display: flex;
  height: 40px;
  align-items: center;
  justify-content: space-between;
}
.item-title {
  font-size: 18px;
  font-weight: 200;
  letter-spacing: 1px;
}
.price {
  color: #f2ab32;
  text-shadow: 1px 2px 8px black;
  font-weight: 700;
}
